import React, { useState } from "react"
import { Link } from "gatsby"
import { useLocation } from "@reach/router"
import pakdoraIcon from "../../images/pakdora-icon.svg"
import "./header.css"

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false)
  const location = useLocation()

  return (
    <header className="header">
      <div className="welcome-bar">
        <span>Welcome to bagomi.au</span>
      </div>
      <div className="header-content">
        <div className="logo-section">
          <div className="logo">
            <img 
              src={pakdoraIcon} 
              alt="Bagomi Logo" 
              className="logo-icon"
            />
            <span className="logo-text">bagomi.au</span>
          </div>
        </div>
        <nav className={`navigation ${isMenuOpen ? 'nav-open' : ''}`}>
          <Link to="/" className={`nav-link ${location.pathname === '/' ? 'active' : ''}`}>Home</Link>
          <Link to="/contact" className={`nav-link ${location.pathname.startsWith('/contact') ? 'active' : ''}`}>Contact</Link>
          <Link to="/about" className={`nav-link ${location.pathname.startsWith('/about') ? 'active' : ''}`}>About us</Link>
        </nav>
        <button 
          className="menu-toggle"
          onClick={() => setIsMenuOpen(!isMenuOpen)}
        >
          <span></span>
          <span></span>
          <span></span>
        </button>
      </div>
    </header>
  )
}

export default Header 